<template>
    <div class="lyrics">
        <div
            class="bgImgBox"
            :style="{ backgroundImage: 'url(' + songData.al.picUrl + ')' }"
        ></div>
        <div class="lyricsTop flexbetween">
            <div class="top-left">
                <div class="iconbox" @click="toBack">
                    <van-icon name="arrow-left" size="24" />
                </div>
                <div class="namebox">
                    <span class="one-ellipsis">{{ songData.name }}</span>
                </div>
            </div>
            <div class="top-right">
                <img src="../assets/img/share.svg" alt />
            </div>
        </div>
        <div class="lyricsBody">
            <div class="songWordBox">
                <ul class="songWordList">
                    <li class="firstSongWordItem"></li>
                    <li
                        :class="
                            item.isActive
                                ? 'songWordItem active'
                                : 'songWordItem'
                        "
                        v-for="(item, index) in songWordData"
                        :key="index"
                        :data-radius="item.time"
                        ref="songWordItem"
                    >
                        <span>{{ item.title }}</span>
                    </li>
                </ul>
            </div>
        </div>
        <div class="lyricsFoot">
            <!-- 进度条 -->
            <div class="sliderBox">
                <div class="showBox"></div>
                <div class="startNum">{{ thisSongData.newDate }}</div>
                <div
                    class="sliderBtn"
                    @touchstart="touchSSliderBtn"
                    @touchmove="touchMsliderBtn"
                    @touchend="touchEsliderBtn"
                ></div>
                <div class="endNum">{{ songData.totalTime }}</div>
            </div>
            <!-- 播放按钮 -->
            <div class="operationBox flexbetween">
                <div class="leftBtn" @click="switchBtn">
                    <img :src="newShowBtn" alt />
                </div>
                <div class="centerBtn flexbetween">
                    <div class="centerBtn_left clearfix" @click="setPrevSong">
                        <div class="sjx_left fl"></div>
                        <div class="sjx_left fl"></div>
                    </div>
                    <div class="centerBtn_center" @click="clickPlay">
                        <img :src="btnUrl" alt />
                    </div>
                    <div class="centerBtn_right clearfix" @click="setNextSong">
                        <div class="sjx_right fr"></div>
                        <div class="sjx_right fr"></div>
                    </div>
                </div>
                <div class="rightBtn" is-link @click="showPopup">
                    <img src="../assets/img/whitelist.svg" alt />
                </div>
                <!-- 弹出盒子 -->
                <van-popup
                    round
                    position="bottom"
                    :style="{ height: '64%' }"
                    v-model="show"
                    @opened="popupOpened"
                    @close="popupClose"
                >
                    <div class="title flexbetween">
                        <div class="titleLeft">
                            <span>当前播放</span>
                            <span class="num">({{ listLength }})</span>
                        </div>
                        <div class="titleRgiht" @click="switchBtn">
                            <span>{{ playStyleText }}</span>
                            <img :src="propNewShowBtn" alt />
                        </div>
                    </div>
                    <div class="listBody">
                        <div
                            class="listBodyItem flexbetween"
                            :class="item.isActive ? 'active' : ''"
                            v-for="(item, index) in songListData"
                            :key="index"
                        >
                            <div
                                class="listBodyItem_left one-ellipsis"
                                @click="clickSongListItem(item, index)"
                            >
                                <div class="text">
                                    <span class="songName">{{
                                        item.name
                                    }}</span>
                                    <span class="songHen">-</span>
                                    <span class="songAuther">{{
                                        item.ar[0].name
                                    }}</span>
                                </div>
                            </div>
                            <div
                                class="listBodyItem_right"
                                @click="delSong(index)"
                            >
                                <img src="../assets/img/close.svg" alt />
                            </div>
                        </div>
                    </div>
                </van-popup>
            </div>
        </div>
    </div>
</template>

<script>
import "../assets/less/lyrics.less";
import { utils } from "../assets/js/utils";
import { createNamespacedHelpers } from "vuex";
const {
    mapState: musicState,
    mapActions: musicActions
} = createNamespacedHelpers("MusicAudioStore");
export default {
    data() {
        return {
            //歌曲信息
            thisSongData: {
                bgImg: "",
                songName: "",
                songImg: "",
                // 总时长
                timeDate: "",
                newDate: ""
            },
            //进度条按钮的判断值
            touchBool: false,
            //定时器
            timeer: null,
            //进度条盒子宽度
            sliderBoxWidth: 0,
            //滑块滑动位置
            sliderBtnMovepagex: 0,
            //播放按钮数据
            btnData: ["whiteplay", "whitesuspend"],
            btnUrl: require("./../assets/img/redplay.svg"),

            //列表数量
            listLength: 0,
            //当前播放类型文字
            playStyleText: "列表循环",
            playStyleTextData: ["列表循环", "单曲循环", "随机播放"],
            //播放类型按钮数据
            styleBtnData: ["whitexh", "whiteonexh", "whitesj"],
            //当前播放类型按钮
            newShowBtn: require("../assets/img/whitexh.svg"),
            show: false,
            // 弹出盒子的按钮,播放类型按钮数据
            propStyleBtnData: ["xh", "onexh", "sj"],
            //弹出盒子当前播放类型按钮
            propNewShowBtn: require("../assets/img/xh.svg")
        };
    },
    computed: {
        ...musicState([
            "songData",
            "audio",
            "iconNum",
            "audioData",
            "songWordData",
            "songListData",
            "playStyleNum"
        ])
    },
    created() {
        this.bgImg = this.audioData.picUrl;
        //设置音乐组件隐藏
        this.setaudioxy(2);
        //设置数据
        this.setData();
        //设置播放图标
        this.btnUrl = require("./../assets/img/" +
            this.btnData[this.iconNum] +
            ".svg");
    },
    mounted() {
        //设置进度条
        this.setsliderBox();
    },
    // 停止定时器
    beforeDestroy() {
        clearInterval(this.timeer);
        this.timeer = null;
    },
    watch: {
        iconNum(val) {
            this.btnUrl = require("./../assets/img/" +
                this.btnData[val] +
                ".svg");
        }
    },
    methods: {
        ...musicActions([
            "setaudioxy",
            "seticonNum",
            "setsongData",
            "setPlayStyleNum"
        ]),

        // 返回
        toBack() {
            this.$router.go(-1);
        },
        //设置数据
        setData() {
            // 歌曲图片
            this.thisSongData.songImg = this.songData.al.picUrl;
            //歌曲名字
            this.thisSongData.songName = this.songData.name;
        },
        //点击播放按钮
        clickPlay() {
            if (this.iconNum == 0) {
                this.seticonNum(1);
                this.audio.play();
                this.setsliderBox();
            } else {
                this.seticonNum(0);
                clearInterval(this.timeer);
                this.timeer = null;
            }
        },

        //设置歌词滚动
        setSongWordRun(gettime) {
            // console.log("this.songWordData :>> ", this.songWordData);
            //纯音乐
            if (this.songWordData[0].nolyric) {
                return;
            } else if (this.songWordData && !this.songWordData[0].nolyric) {
                let songWordList = document.querySelector(".songWordList");
                let timeS = utils.setGoS(gettime);
                let maxTime = utils.setGoS(
                    this.songWordData[this.songWordData.length - 1].time
                );
                //进行中的时间没有歌词
                if (timeS > maxTime) {
                    for (let k = 0; k < this.songWordData.length; k++) {
                        if (this.songWordData[k].isActive) {
                            this.songWordData[k].isActive = false;
                            break;
                        }
                    }
                    this.songWordData[
                        this.songWordData.length - 1
                    ].isActive = true;
                    songWordList.style.top =
                        -36 * this.songWordData.length - 1 + "px";
                    return;
                }
                for (let i = 0; i < this.songWordData.length; i++) {
                    if (
                        utils.setGoS(this.songWordData[i].time) == timeS ||
                        timeS < utils.setGoS(this.songWordData[i + 1].time)
                    ) {
                        songWordList.style.top = -36 * i + "px";
                        for (let k = 0; k < this.songWordData.length; k++) {
                            if (this.songWordData[k].isActive) {
                                this.songWordData[k].isActive = false;
                                break;
                            }
                        }
                        // 歌词的时间有时是结束时间有些是开始时间，目前设置开始时间
                        this.songWordData[i].isActive = true;
                        break;
                    } else {
                    }
                }
            }
        },

        //设置进度条操作
        setNowSlider() {
            let startS = Math.floor(this.audio.currentTime) % 60;
            let startM = Math.floor((this.audio.currentTime / 60) % 60);
            //设置进行中的时间
            this.thisSongData.newDate = startM + "." + startS;
            //设置合适的歌词时间格式
            startM = startM <= 9 ? "0" + startM : startM;
            startS = startS <= 9 ? "0" + startS : startS;
            //设置滚动歌词
            this.setSongWordRun(startM + ":" + startS);
            //计算比例
            let num = this.songData.dt / 1000 / this.audio.currentTime;
            //设置宽度
            document.querySelector(".showBox").style.width = 100 / num + "%";
            //设置滑块位置
            document.querySelector(".sliderBtn").style.left =
                this.sliderBoxWidth / num + "px";
        },

        //设置进度条
        setsliderBox() {
            clearInterval(this.timeer);
            this.timeer = null;
            this.sliderBoxWidth = document.getElementsByClassName(
                "sliderBox"
            )[0].offsetWidth;
            //初始化进度条
            this.setNowSlider();
            if (this.iconNum == 1) {
                //定时器
                this.timeer = setInterval(() => {
                    // 设置进度条函数
                    this.setNowSlider();
                }, 1000);
            } else {
                clearInterval(this.timeer);
                this.timeer = null;
            }
        },

        //按下滑块
        touchSSliderBtn(e) {
            this.touchBool = true;
            clearInterval(this.timeer);
            this.timeer = null;
        },

        // 滑块移动
        touchMsliderBtn(e) {
            if (this.touchBool) {
                this.sliderBtnMovepagex = e.changedTouches[0].pageX;
                //获取进度条盒子距离左边宽度
                let sliderBoxLeft = document.querySelector(".sliderBox")
                    .offsetLeft;

                //判断滑块位置值
                let sliderBtnLfet = 0;
                if (this.sliderBtnMovepagex - sliderBoxLeft <= 0) {
                    sliderBtnLfet = 0;
                } else if (
                    this.sliderBtnMovepagex - sliderBoxLeft >=
                    this.sliderBoxWidth
                ) {
                    sliderBtnLfet = this.sliderBoxWidth;
                } else {
                    sliderBtnLfet = this.sliderBtnMovepagex - sliderBoxLeft;
                }
                // 设置滑块位置
                document.querySelector(".sliderBtn").style.left =
                    sliderBtnLfet + "px";
                //设置进度条长度
                document.querySelector(".showBox").style.width =
                    sliderBtnLfet + "px";

                //设置当前音乐播放时间以及显示
                this.setStartData();
            } else {
                return;
            }
        },

        //离开滑块
        touchEsliderBtn() {
            //设置进度条
            this.setsliderBox();
            if (
                document.querySelector(".showBox").offsetWidth ==
                this.sliderBoxWidth
            ) {
                return;
            } else {
                this.audio.play();
                this.seticonNum(1);
            }
        },

        //设置当前音乐播放时间以及显示
        setStartData() {
            let showboxwidth = document.querySelector(".showBox").offsetWidth;
            let num = this.sliderBoxWidth / showboxwidth;
            this.thisSongData.newDate =
                Math.floor((this.songData.dt / 1000 / num / 60) % 60) +
                "." +
                Math.floor((this.songData.dt / 1000 / num) % 60);
            this.audio.currentTime = Math.floor(this.songData.dt / 1000 / num);
        },

        //下一首
        setNextSong() {
            //设置进度条
            clearInterval(this.timeer);
            this.timeer = null;
            this.setsliderBox();
            this.seticonNum(1);

            let songId = this.audio.getAttribute("data-id");
            //下一首列表循环
            if (this.playStyleNum == 0) {
                for (let i = 0; i < this.songListData.length; i++) {
                    if (songId == this.songListData[i].id) {
                        let nowPlay = 0;
                        if (i + 1 == this.songListData.length) {
                            nowPlay = 0;
                        } else {
                            nowPlay = i + 1;
                        }
                        this.setsongData(this.songListData[nowPlay]).then(
                            audio => {
                                audio.play();
                            }
                        );
                        this.songListData[nowPlay].isActive = true;
                    } else {
                        this.songListData[i].isActive = false;
                    }
                }
            } //下一首随机播放
            else if (this.playStyleNum == 2) {
                for (let i = 0; i < this.songListData.length; i++) {
                    if (songId == this.songListData[i].id) {
                        let nowPlay = Math.ceil(
                            Math.random() * this.songListData.length - 1
                        );
                        this.setsongData(this.songListData[nowPlay]).then(
                            audio => {
                                audio.play();
                            }
                        );
                        this.songListData[nowPlay].isActive = true;
                    } else {
                        this.songListData[i].isActive = false;
                    }
                }
            }
        },

        //上一首
        setPrevSong() {
            //设置进度条
            clearInterval(this.timeer);
            this.timeer = null;
            this.setsliderBox();
            this.seticonNum(1);
            let songId = this.audio.getAttribute("data-id");
            //上一首列表循环
            if (this.playStyleNum == 0) {
                for (let i = 0; i < this.songListData.length; i++) {
                    if (songId == this.songListData[i].id) {
                        let nowPlay = 0;
                        if (i - 1 < 0) {
                            nowPlay = this.songListData.length - 1;
                        } else {
                            nowPlay = i - 1;
                        }
                        this.setsongData(this.songListData[nowPlay]).then(
                            audio => {
                                audio.play();
                            }
                        );
                        this.songListData[nowPlay].isActive = true;
                    } else {
                        this.songListData[i].isActive = false;
                    }
                }
            } else if (this.playStyleNum == 2) {
                for (let i = 0; i < this.songListData.length; i++) {
                    if (songId == this.songListData[i].id) {
                        let nowPlay = Math.ceil(
                            Math.random() * this.songListData.length - 1
                        );
                        this.setsongData(this.songListData[nowPlay]).then(
                            audio => {
                                audio.play();
                            }
                        );
                        this.songListData[nowPlay].isActive = true;
                    } else {
                        this.songListData[i].isActive = false;
                    }
                }
            }
        },

        //点击切换播放模式
        switchBtn() {
            let setNum = this.playStyleNum + 1;
            if (setNum >= this.styleBtnData.length) {
                setNum = 0;
                this.setPlayStyleNum(0);
            }
            this.playStyleText = this.playStyleTextData[setNum];
            this.propNewShowBtn = require("../assets/img/" +
                this.propStyleBtnData[setNum] +
                ".svg");
            this.newShowBtn = require("../assets/img/" +
                this.styleBtnData[setNum] +
                ".svg");
            this.setPlayStyleNum(setNum);
        },

        //设置弹出框的title
        popupOpened() {
            document.querySelector(".title").style.position = "fixed";
        },

        //设置弹出框的title
        popupClose() {
            document.querySelector(".title").style.position = "static";
        },

        //点击删除
        delSong(index) {
            // 设置播放列表数量

            //随机
            if (this.playStyleNum == 2) {
                let randomNum = Math.ceil(Math.random() * this.listLength - 1);
                for (let i = 0; i < this.songListData.length; i++) {
                    if (this.songListData[i].isActive) {
                        if (i == index) {
                            this.setsongData(this.songListData[randomNum]).then(
                                audio => {
                                    audio.play();
                                }
                            );
                            this.songListData[randomNum].isActive = true;
                        } else {
                        }
                    }
                }
            } else {
                //其他
                for (let i = 0; i < this.songListData.length; i++) {
                    if (this.songListData[i].isActive) {
                        if (i == index) {
                            this.setsongData(this.songListData[i + 1]).then(
                                audio => {
                                    audio.play();
                                }
                            );
                            this.songListData[i + 1].isActive = true;
                        }
                    }
                }
            }

            this.songListData.splice(index, 1);
            this.listLength = this.songListData.length;
        },

        //点击显示播放歌单
        showPopup() {
            this.show = true;
            // 设置播放列表数量
            this.listLength = this.songListData.length;
            let songId = this.audio.getAttribute("data-id");
            this.songListData.map(v => {
                if (v.id == songId) {
                    v.isActive = true;
                } else {
                    v.isActive = false;
                }
            });
        },

        //点击歌单的歌曲
        clickSongListItem(item, index) {
            if (item.isActive) {
                return;
            }
            for (let i = 0; i < this.songListData.length; i++) {
                if (this.songListData[i].isActive) {
                    this.songListData[i].isActive = false;
                    break;
                }
            }
            item.isActive = true;
            this.setsongData(item).then(audio => {
                audio.play();
            });
            this.seticonNum(1);
        }

        //
    }
};
</script>

